
"use client";
import { cn } from "@/lib/utils";
import { MapPin } from "lucide-react";

interface SvgMapProps {
    pickup: string;
    dropoff: string;
    onCityClick: (cityName: string) => void;
    selecting: 'pickup' | 'dropoff' | null;
}

const cities = [
    { name: "Riyadh", x: "48%", y: "45%" },
    { name: "Jeddah", x: "15%", y: "65%" },
    { name: "Dammam", x: "80%", y: "55%" },
    { name: "Mecca", x: "20%", y: "72%" },
    { name: "Medina", x: "22%", y: "50%" },
    { name: "Tabuk", x: "5%", y: "20%" },
];

export function SvgMap({ pickup, dropoff, onCityClick, selecting }: SvgMapProps) {
    const pickupCity = cities.find(c => c.name === pickup);
    const dropoffCity = cities.find(c => c.name === dropoff);

    return (
        <div className="relative w-full h-full bg-muted/30">
            <svg
                viewBox="0 0 200 200"
                xmlns="http://www.w3.org/2000/svg"
                className="w-full h-full"
            >
                {/* Simple representation of Saudi Arabia */}
                <path
                    d="M 50,5 L 150,15 L 190,80 L 160,195 L 40,190 L 10,100 Z"
                    fill="hsl(var(--card))"
                    stroke="hsl(var(--border))"
                    strokeWidth="1"
                />

                {/* Draw line between pickup and dropoff */}
                {pickupCity && dropoffCity && (
                     <line
                        x1={pickupCity.x}
                        y1={pickupCity.y}
                        x2={dropoffCity.x}
                        y2={dropoffCity.y}
                        stroke="hsl(var(--primary))"
                        strokeWidth="1"
                        strokeDasharray="2 2"
                    />
                )}

                {/* City markers */}
                {cities.map((city) => (
                    <g
                        key={city.name}
                        transform={`translate(${city.x.replace('%','') * 2 - 10}, ${city.y.replace('%','') * 2 - 10})`}
                        className={cn("cursor-pointer transition-transform duration-200 hover:scale-110", selecting && "animate-pulse")}
                        onClick={() => onCityClick(city.name)}
                    >
                         <foreignObject x="0" y="0" width="20" height="20">
                           <div className="w-full h-full flex items-center justify-center">
                             <MapPin className={cn(
                                "w-5 h-5",
                                pickup === city.name ? "text-green-500 fill-green-200" :
                                dropoff === city.name ? "text-red-500 fill-red-200" :
                                "text-muted-foreground/50 fill-muted"
                             )} />
                           </div>
                         </foreignObject>
                         <text x="10" y="25" textAnchor="middle" fontSize="6" fill="hsl(var(--foreground))" className="font-sans">
                            {city.name}
                        </text>
                    </g>
                ))}
            </svg>
        </div>
    );
}
